import props from './props.js';
import { addUnit, getPx } from '../common/utils/add-unit';
import { style as styleUtil } from '../common/utils/style';
import { errorTip } from '../common/utils/validator';

import PressIconPlus from '../press-icon-plus/press-icon-plus.vue';
import PressTransition from '../press-transition/press-transition.vue';







export default {
  name: 'PressBackTop',
  options: {
    styleIsolation: 'shared',
  },
  components: {
    PressIconPlus,
    PressTransition,
  },
  mixins: [props],
  emits: [
    'click',
  ],
  computed: {
    backTopStyle() {
      // 
      const style = {
        bottom: addUnit(this.bottom),
        right: addUnit(this.right),
        width: addUnit(this.size),
        height: addUnit(this.size),
        position: 'fixed',
        zIndex: this.zIndex,
      };
      return styleUtil(style);
    },
    show() {
      return getPx(this.scrollTop) > getPx(this.top);
    },
    innerIconStyle() {
      return styleUtil([{
        color: '#909399',
        fontSize: '19px',
      }, this.iconStyle]);
    },
    contentStyle() {
      const style = {};
      let radius = 0;
      // 
      if (this.mode === 'circle') {
        radius = '100px';
      } else {
        radius = '4px';
      }
      // nvue
      style.borderTopLeftRadius = radius;
      style.borderTopRightRadius = radius;
      style.borderBottomLeftRadius = radius;
      style.borderBottomRightRadius = radius;
      return styleUtil([style, this.customStyle]);
    },
  },
  methods: {
    backToTop() {










      if (typeof this.scrollToTop === 'function') {
        this.scrollToTop();
      } else {
        uni.pageScrollTo({
          scrollTop: 0,
          duration: this.duration,
        });
      }


      this.$emit('click');
    },
  },
};
